/* Standard */

$white: hsl(0, 0%, 100%);
$black: hsl(0, 0%, 0%);

$transparent-white: hsla(0, 0%, 100%, 0%);
$transparent-black: hsla(0, 0%, 0%, 0%);

/* Grey */

$grey-010: hsl(0, 0%, 90%);
$grey-020: hsl(0, 0%, 84%);
$grey-030: hsl(0, 0%, 72%);
$grey-040: hsl(0, 0%, 52%);
$grey-050: hsl(0, 0%, 42%);
$grey-060: hsl(0, 0%, 33%);
$grey-070: hsl(0, 0%, 27%);
$grey-080: hsl(0, 0%, 23%);
$grey-090: hsl(0, 0%, 21%);
$grey-100: hsl(0, 0%, 18%);
$grey-110: hsl(0, 0%, 16%);
$grey-120: hsl(0, 0%, 13%);

/* Primary */

$primary-010: hsl(190, 80%, 92%);
$primary-020: hsl(190, 68%, 82%);
$primary-030: hsl(190, 58%, 70%);
$primary-040: hsl(190, 52%, 57%);
$primary-050: hsl(190, 50%, 46%);
$primary-060: hsl(190, 55%, 41%);
$primary-070: hsl(190, 62%, 36%);
$primary-080: hsl(190, 75%, 30%);
$primary-090: hsl(190, 85%, 24%);

/* Accents */

$green-010: hsl(80, 86%, 92%);
$green-020: hsl(80, 71%, 78%);
$green-030: hsl(80, 57%, 61%);
$green-040: hsl(80, 52%, 52%);
$green-050: hsl(80, 52%, 46%);
$green-060: hsl(80, 63%, 38%);
$green-070: hsl(80, 71%, 31%);
$green-080: hsl(80, 76%, 25%);
$green-090: hsl(80, 80%, 20%);

$red-010: hsl(342, 92%, 92%);
$red-020: hsl(342, 83%, 81%);
$red-030: hsl(342, 80%, 66%);
$red-040: hsl(342, 72%, 58%);
$red-050: hsl(342, 66%, 52%);
$red-060: hsl(342, 69%, 47%);
$red-070: hsl(342, 74%, 42%);
$red-080: hsl(342, 86%, 35%);
$red-090: hsl(342, 90%, 28%);

$purple-010: hsl(267, 94%, 92%);
$purple-020: hsl(267, 78%, 82%);
$purple-030: hsl(267, 62%, 70%);
$purple-040: hsl(267, 54%, 62%);
$purple-050: hsl(267, 48%, 54%);
$purple-060: hsl(267, 52%, 48%);
$purple-070: hsl(267, 70%, 42%);
$purple-080: hsl(267, 80%, 37%);
$purple-090: hsl(267, 81%, 31%);

$orange-050: hsl(30, 60%, 54%);
$yellow-050: hsl(50, 64%, 52%);

/* Shadows */

$shadow-010: hsla(0, 0%, 0%, 5%);
$shadow-020: hsla(0, 0%, 0%, 10%);
$shadow-030: hsla(0, 0%, 0%, 15%);
$shadow-040: hsla(0, 0%, 0%, 20%);
$shadow-050: hsla(0, 0%, 0%, 25%);

/* Backgrounds */

$background-library: $grey-080;

$background-toolbar: $grey-070;

$background-scrollbar: $grey-080;
$background-scrollbar-light: $grey-070;
$background-scrollbar-lightest: $grey-060;

$background-nav-window-nav-bar: $grey-100;
$background-nav-window-panel: $grey-080;

$background-modal-heading: $grey-100;
$background-modal: $grey-080;

$background-overlay: hsla(0, 0%, 0%, 50%);

$background-tabbar: $grey-100;
$background-tabbar-condensed: $grey-080;

/* Text */

$text-primary: $white;
$text-secondary: $grey-030;
$text-tertiary: $grey-050;

/* Buttons */

$button-text: $white;
$button-text-hover: $grey-020;
$button-text-tertiary: $grey-030;

/* Inputs */

$input-text: $grey-020;
$input-text-hover: $white;

$input-selected: $primary-050;
$input-selected-hover: $primary-070;

$disabled-opacity: 0.6;

/* Inset */

$inset-background: $grey-100;
$inset-background-hover: $grey-110;
$inset-border: $grey-060;
$inset-border-hover: $grey-070;

/* Highlights */

$tab-highlight: $primary-060;
$tab-highlight-condensed: $grey-110;

$text-selection: $primary-050;

$slider-fill: $primary-060;
$slider-fill-hover: $primary-070;

/* Exports */

:export {
  backgroundLibrary: $background-library;
}
