$breakpoint-sm: 480px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1280px;

@mixin breakpoint-xs {
  @media (max-width: $breakpoint-sm) {
    @content;
  }
}

@mixin breakpoint-sm-down {
  @media (max-width: $breakpoint-md) {
    @content;
  }
}

@mixin breakpoint-sm {
  @media (min-width: $breakpoint-sm + 1) and (max-width: $breakpoint-md) {
    @content;
  }
}

@mixin breakpoint-sm-up {
  @media (min-width: $breakpoint-sm + 1) {
    @content;
  }
}

@mixin breakpoint-md-down {
  @media (max-width: $breakpoint-lg) {
    @content;
  }
}

@mixin breakpoint-md {
  @media (min-width: $breakpoint-md + 1) and (max-width: $breakpoint-lg) {
    @content;
  }
}

@mixin breakpoint-md-up {
  @media (min-width: $breakpoint-md + 1) {
    @content;
  }
}

@mixin breakpoint-lg-down {
  @media (max-width: $breakpoint-xl) {
    @content;
  }
}

@mixin breakpoint-lg {
  @media (min-width: $breakpoint-lg + 1) and (max-width: $breakpoint-xl) {
    @content;
  }
}

@mixin breakpoint-lg-up {
  @media (min-width: $breakpoint-lg + 1) {
    @content;
  }
}

@mixin breakpoint-xl {
  @media (min-width: $breakpoint-xl + 1) {
    @content;
  }
}

/* stylelint-disable order/properties-order */

:export {
  sm: $breakpoint-sm;
  md: $breakpoint-md;
  lg: $breakpoint-lg;
  xl: $breakpoint-xl;
}
